.card {
  box-sizing: border-box;
  width: 200px;
  background-color: #fff;
  border-radius: 5px;
  border: 1px solid $card-border-color;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 32px;
  transition-property: transform;
  transition-duration: $transition-time;

  &:hover {
    transform: scale(1.2);
  }

  &__image {
    width: 160px;
    height: 134px;
    margin-bottom: 40px;
  }

  &__content {
    padding: 0 16px;
    display: flex;
    flex-direction: column;
  }

  &__title {
    font-weight: 500;
    line-height: 18px;
    font-size: 12px;
    margin: 0 0 4px;
    transition-property: color;
    transition-duration: $transition-time;
  }

  &__code {
    line-height: 14px;
    font-size: 10px;
    color: $secondary-font-color;
    margin: 0 0 16px;
  }

  &__rating {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 24px;

    &-review {
      margin: 0;
    }
  }

  &__price {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 0 16px;

    &-title {
      font-size: 12px;
      line-height: 18px;
      color: $secondary-font-color;
      margin: 0;
    }

    &-value {
      font-weight: 700;
      font-size: 16px;
      line-height: 18px;
      margin: 0;
    }
  }

  &__button {
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid $button-color;
    background-color: $button-color;
    border-radius: 5px;
    width: $button-width;
    height: $button-height;
    font-size: 14px;
    font-weight: 700;
    text-decoration: 0;
    color: #fff;
    text-transform: uppercase;
    margin-bottom: 16px;
    transition-property: color, background-color;
    transition-duration: $transition-time;

    &:hover {
      background-color: transparent;
      color: $button-color;
    }
  }
}

.card:hover .card__title {
  color: $hover-text-color;
}
